@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

body{
    font: 400 14px Roboto, sans-serif;
    background: #f0f0f5;
    -webkit-font-smoothing: antialiased;
}

input, button, textarea {
    font: 400 18px Roboto, sans-serif;
}

button {
    cursor: pointer;
}

form input{
    width: 100%;
    height: 40px;
    color: #333;
    border: 1px solid #dcdce6;
    border-radius: 8px;
    padding: 0 20px;
}

.button {
    width: 100%;
    height: 40px;
    background: #e02041;
    border: 0;
    border-radius: 8px;
    color: #fff;
    font-weight: 700;
    margin-top: 16px;
    display: inline-block;
    text-decoration: none;
    font-size: 18px;
    line-height: 60px;
}

.button:hover {
    -webkit-filter: brightness(90%);
            filter: brightness(90%);
}
 h1{
     color: #7159c1;
 }

 .login-container{
     width: 100%;
     max-width: 1120px;
     height: 100vh;
     margin: 0 auto;
     display: flex;
     align-items: center;
     justify-content: center;
 } 

 .login-container section.form{
    width: 100%;
    max-width: 350px;
    margin-right: 30px;
 }

 .login-container section.form form{
     margin-top: 100px;
 }

 .login-container section.form form h1{
     font-size: 32px;
     margin-bottom: 32px;
 }
